function getTime(){
	var time = new Date();
	// time.getYear(); //
	// time.getFullYear(); //(4,1970-???)
	// time.getMonth(); //(0-11,01)
	// time.getDate(); //(1-31)
	// time.getDay(); //X(0-6,0)
	// time.getTime(); //(1970.1.1)
	// time.getHours(); //(0-23)
	// time.getMinutes(); //(0-59)
	// time.getSeconds(); //(0-59)
	// time.getMilliseconds(); //(0-999)
	// time.toLocaleDateString(); //
	// time.toLocaleTimeString(); //
	// time.toLocaleString( ); //
	return  time.getFullYear()+'-'+(time.getMonth()<10?'0'+time.getMonth():time.getMonth())+'-'+
			(time.getDate()<10?'0'+time.getDate():time.getDate())+' '+
			(time.getHours()<10?'0'+time.getHours():time.getHours())+':'+
			(time.getMinutes()<10?'0'+time.getMinutes():time.getMinutes())+':'+
			(time.getSeconds()<10?'0'+time.getSeconds():time.getSeconds());
}

export{
	getTime
}